<template>
  <div class="row">
        <p>Numeros Ocupados = <b>{{ocupados}}</b></p>
      <etiquetaNumeroPrivado class="col"
      v-for="n in numeros" :key="n.numero"
      :numero = "n.numero"
      :ocupado = "n.ocupado"
      :cliente = "n.cliente"
      />
</div>
  
</template>

<script>
import { defineAsyncComponent } from 'vue'
import { mapActions, mapGetters } from 'vuex'

export default {
    data(){
        return{
            numeros:[],
            ocupados:0
        }
    },
    components:{
        etiquetaNumeroPrivado : defineAsyncComponent(()=> import('./etiquetaNumeroPrivado.vue'))
    },
    computed:{
        ...mapGetters('numeros',['getNumerosAll'])
    },
    methods:{
        ...mapActions('numeros',['getNumeros'])
    },
    created(){
        this.getNumeros()

        var numerosAll = this.getNumerosAll
        var nOcupados = 0
        numerosAll.forEach(n => {
            if(!n.cliente){
                n.cliente = '0'
            }else{
                nOcupados += 1
            }
        });

        this.numeros = numerosAll
        this.ocupados = nOcupados

    }
}
</script>

